import React from 'react'
import { Icon, Step } from 'semantic-ui-react'

const Steps = () => (
    <Step.Group>
      <Step>
        <Icon name='in cart' />
        <Step.Content>
          <Step.Title>Pedido</Step.Title>
          <Step.Description>Arm tu pedido indicando archivo, material y dimensiones.</Step.Description>
        </Step.Content>
      </Step>

      <Step>
        <Icon name='payment' />
        <Step.Content>
          <Step.Title>Cotizacin</Step.Title>
          <Step.Description>Te lo cotizamos y lo pags online.</Step.Description>
        </Step.Content>
      </Step>

      <Step>
        <Icon name='truck' />
        <Step.Content>
          <Step.Title>Envo</Step.Title>
          <Step.Description>Pods recibirlo en tu casa o pasar a buscarlo.</Step.Description>
        </Step.Content>
      </Step>
    </Step.Group>
);

export default Steps


